/********** Extra Extra small (xxs) devices only **********/
@media (max-width: 576px) {
  .comment_input_container {
    display: block;
  }

  button.comment_input_button {
    margin-left: 0px;
  }

  input[name="comment"] {
    margin-bottom: 1em;
    width: 100%;
    padding: 2em;
    outline: none;
    border: 1px solid #000;
  }

  .comment_input_username {
    margin-bottom: 1em;
  }

  .mobile_commentlist_hr_hidden {
    display: none;
  }
}
