body {
  background: #fff5f5;
}

.board_filter_container {
  display: inline;
}

.board_filter_option {
  font-size: 15px;
  color: #000;
  font-weight: 100;
  padding: 0.1em 0.75em 0.1em 0.75em;
  margin-right: 10px;
  text-align: center;
  width: fit-content;
  background-color: #fff5f5;
  border-bottom: none;
  border-top: none;
  border-left: none;
  border-right: none;
  border: 1px solid #000;
}

.board_filter_option:focus {
  font-size: 15px;
  color: #fff;
  font-weight: 100;
  padding: 0.1em 0.75em 0.1em 0.75em;
  margin-right: 10px;
  text-align: center;
  width: fit-content;
  background-color: #000;
  border-bottom: none;
  border-top: none;
  border-left: none;
  border-right: none;
  border: 1px solid #000;
  outline: none;
}

.board_filter_option:active {
  font-size: 15px;
  color: #fff;
  font-weight: 100;
  padding: 0.1em 0.75em 0.1em 0.75em;
  margin-right: 10px;
  text-align: center;
  width: fit-content;
  background-color: #000;
  border-bottom: none;
  border-top: none;
  border-left: none;
  border-right: none;
  border: 1px solid #000;
  outline: none;
}

