/********** Small devices only **********/
@media (min-width: 768px) and (max-width: 991px) {
  .board_filter_wrapper {
    margin-top: 70px;
  }
}

/********** Extra small devices only **********/
@media (min-width: 577px) and (max-width: 767px) {
  .board_filter_wrapper {
    margin-top: 70px;
  }
}

@media (max-width: 576px) {
  .board_filter_wrapper {
    margin-top: 70px;
  }

  .board_filter_container {
    display: inline-grid;
  }

  .board_filter_option {
    margin-bottom: 10px;
  }
}
